* {
    margin: 0;
    padding: 0;
}

.intro {
    display: flex;
    height: 100vh;
}

.intro-left {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
   
}

.intro-right {
    flex: 1;
    position: relative;
}

.left-wrapper {
    padding: 70px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-title {
    font-size: 30px;
    font-weight: 300;
}

.intro-name {
    font-size: 60px;
}

.intro-titles {
    height: 50px;
    overflow: hidden;
}

.titles-wrapper {
    height: 100%;
}


.title {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(25, 129, 25);
    display: flex;
    align-items: center;
}

.intro-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;   
}
.img-background {
    clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color:rgb(25, 129, 25);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.intro-btn {
    padding: 8px 16px;
    text-align: center;
    color: rgb(25, 129, 25);
    border-radius: 8px;
    border: 2px solid rgb(25, 129, 25);
    text-decoration: none;
    width: 20%;
    transition: all .5s;
}

.intro-btn:hover {
    background-color: rgb(25, 129, 25);
    color: white;
}

@media screen and (max-width: 480px) {
    .intro {
        flex-direction: column;
    }

    .left-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .intro-right {
    width: 100%;

    }

    .intro-description {
        display: none;
    }
    .intro-btn {
        margin-top: 20px;
    }
}
