.product-list {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pl-title {
    font-size: 50px;
    font-weight: 600;
    color: rgb(29,125,29)
}

.pl-description {
    margin: 20px 0;
    font-size: 1.4rem;
}

.pl-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

@media screen and (max-width: 480px) {
    .product-list {
        padding: 10px;
        margin-top: 20px;
    }
    .pl-text {
        width: 100%;
    }

    .pl-description {
        font-size: 1rem;
    }
}