.product-container {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    position: relative;
}

.product-capa {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, .5);
    top: 0;
    left: 0;
    transform: translateX(-100%);
    transition: all .5s;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-link {
    font-size: 1.5rem;
    margin-left: 12px;
}

.fa-github{
    font-size: 1.5rem;
}

.product-container:hover > .product-capa {
    transform: translateX(0)
}

.product-capa:hover {
    transform: translateX(0)
}

.product-link{
    color:white
}

.product-browser {
    width: 100%;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}


.product-img {
    width: 100%;
    object-fit: cover;
    height: 100%;
}


.product-title {
    color: black;
    font-weight: 500;
}

@media screen and (max-width: 480px) {
    .product-container {
        width: 100%;
    }

    .product-img {
        width: 100%;
    }
    .product-img:hover {
        transform: none;
    }

}
@media screen and (max-width: 768px) {
    .product-container {
        width: 100%;
    }
    .product-img {
        width: 100%
    }
    .product-img:hover {
        transform: none;
    }
}