.about {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
}

.about-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.about-right {
    flex: 1;
    height: 100%;
    width: 100%;
}

.about-card {
    width: 60%;
    height: 70vh;
    border-radius: 8px;
    position: relative;
}

.about-card.bg {
    position: absolute;
    background-color: rgb(25,129,25);
    top: 50px;
    left: 90px;
}

.about-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;
}

.about-title {
    margin-top: 50px;
    color: rgb(25,129,25)
}

.about-subtitle {
    margin-top: 10px;
}

.about-description {
    margin-top: 20px;
}

@media screen and (max-width: 480px) {
    .about {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
        width: 100%;
    }

    .about-left {
        width: 100%;
        display:none;
    }
    .about-right {
       width: 90%;
    }

    .about-card {
        height: 30vh;
    }
    
    .about-card.bg {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .about {
        height: 50vh;
        margin-top: 50px;
    }
    .about-left{
        height: 100%;
    }

    .about-card.bg {
       height: 300px;
    }

    .about-img {
        height: 50%;
    }
}