.contact {
    height: 100vh;
    position: relative;
}

.contact-background {
    width:20px;
    height: 100%;
    background-color: rgb(25,129,25);
    position: absolute;
}

.contact-container {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 50px;
}

.contact-left {
    flex: 1;
}

.contact-right{
    flex: 1;
    position: relative;
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: white;
    font-family: sans-serif;
}

#form {
    background-color: white;
    width: 400px;
    border-radius: 8px;
    padding: 20px 40px;
    box-shadow: 0 10px 25px rgba(92, 99, 105, .2);
}

.title {
    font-size: 2rem;
    margin-bottom: 40px;
}

.input-container {
    position: relative;
    height: 45px;
    width: 90%;
    margin-bottom: 15px;
}

.input {
   
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border: 1px solid #DADCE0;
    border-radius: 7px;
    font-size: 16px;
    padding: 0 20px;
    outline: none;
    background: none;
z-index: 1;
}

::placeholder {
    color: transparent;
}

.label {
    position: absolute;
    top: 15px;
    left: 15px;
    padding: 0 4px;
    background-color: white;
    color: #DADCE0;
    font-size: 16px;
    transition: 0.5s;
    z-index: 0;
}

.submitBtn {
    display: block;
    margin-left: auto;
    padding: 15px 30px;
    border: none;
    background-color: rgb(25,129,25);
    color: white;
    border-radius: 6px;
    font-size: 1rem;
    margin-top: 70px;
    cursor: pointer;
    transition: 0.3s;
}

.submitBtn:hover {
    background-color: rgb(25,129,25);
    transform: translateY(-2px);
}

.input:focus + .label {
    top: -7px;
    left: 3px;
    z-index: 41;
    font-size: 0.9rem;
    font-weight: 600;
    color: rgb(25,129,25);
}

.input:focus {
    border: 2px solid rgb(25,129,25);
  }

  .input:not(:placeholder-shown)+ .label {
    top: -7px;
    left: 3px;
    z-index: 41;
    font-size: 14px;
    font-weight: 600;
  }

  textarea {
      width: 100%;
      height: 80px;
      resize: none;
      border-radius: 8px;
      border: 2px solid #DADCE0;
      padding: 10px;
  }

  textarea:focus {
      outline:none;
  }

  .contact-img {
      width: 100%;

      object-fit: cover;
      animation: contact 8s ease-in-out infinite alternate;
  }

  @keyframes contact {
      0% {
          transform: translateY(0);
      }
      50% {
          transform: translateY(-10%);
      }
      100% {
          transform: translateY(10%);
      }
  }

  .contact-circle {
      width: 70px;
      height:70px;
      border-radius: 50%;
      background-color: rgba(25,129,25);
      animation: contact 2s ease-in-out infinite alternate;
      position: absolute;
  }

  .circle-left {
      top: 40px;
  }

  .circle-right {
      right: 10px;
  }

  .circle-rightup {
      top: 0;
      right: 10px;
  }

  .exitoso {
      color: rgb(25,129,25);
      margin-top: 10px;
      font-weight: 600;
      display: none;
  }
  .contact-title{
      color: rgb(25,129,25);
  }

  @media screen and (max-width: 480px) {
      .contact-container {
          width: 60%;
          flex-direction: column;
          padding: 0px 50px;
          margin-top: 50px;
          overflow: hidden;
      }
      
      .contact-background {
          display: none;
      }
      .contact-title {
          font-size: 30px;
      }
      .contact-left{
          width: 300px;
          margin-right: 10px;
          height: 100%;
      }
      #form {
          width: 100%;
          height: 100%;
      }
      .input {
          width: 200px;
      }
       textarea {
           width: 200px;
       }
      .contact-right {
          margin-top: 100px;
          width: 100%;
          height:100%;
      }

      .submitBtn {
          margin-left: 0;
      }
      .contact {
          height: 100%;
      }

      .circle-right {
          display: none;
      }
      .circle-leftb{
          display: none;
      }
  }

  @media screen and (max-width: 768px) {
      .contact-container {
          flex-direction: column;
      }

  }